body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

@media (max-width: 575px){#home>section>h2{font-size:3.5rem}#events>ul>li{width:75%}#vcn{padding-bottom:9rem}}@media (min-width: 576px){#vcn{padding-bottom:4rem}}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}@keyframes fadein{from{opacity:0}to{opacity:1}}.text-maroon{color:#b00000}a.text-maroon:hover{color:#900000}.fade-in{opacity:0;-webkit-animation:fadein 1s forwards;animation:fadein 1s forwards}.bg-maroon{background-color:#b00000}button.bg-maroon:hover{background-color:#900000}#root{position:relative;min-height:100vh}ul.nav.nav-tabs a{color:#A9A9A9;cursor:pointer}ul.nav.nav-tabs a.active{font-weight:bold}header>h1{position:fixed;left:1rem;z-index:1031;font-size:medium}header>h1>a>img{height:2rem}nav{-webkit-transition:1s;transition:1s}nav img{height:2rem}nav ul li{padding-left:5px}nav ul li a{border-radius:5px;border:3px solid transparent}nav ul li a.active{border:3px solid #777777}nav ul li a:hover{border:3px solid #777777}#home{background:url(/vsauiuc/static/media/front_page.16f39d58.jpg) center/cover}#home>section{background-color:rgba(40,40,40,0.7)}#about .board-members{list-style-type:none;margin-top:1rem;padding:0;text-align:center;font-size:1.25rem;font-weight:bold}#events .event-date{position:absolute;left:7.5%;top:-5%;width:3.5rem}#events .event-date .event-month{background:#696969;height:1.5rem;line-height:1.5rem}#events .event-date .event-day{background:#f4b35b;height:2.5rem;line-height:2.5rem;font-size:2rem}#events .event-info span{margin-left:0.5rem}#contact ul{list-style-type:none;font-size:1.25rem}

